'use client';

import { useEffect } from 'react';
import { useFormState, useFormStatus } from 'react-dom';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { submitContactForm, type ContactFormState } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from '@/hooks/use-toast';
import { Mail, Phone, MapPin, Facebook, Instagram } from 'lucide-react';
import Link from 'next/link';

const contactFormSchema = z.object({
  name: z.string().min(3, 'Nombre es requerido.'),
  email: z.string().email('Correo electrónico inválido.'),
  message: z.string().min(10, 'Mensaje debe tener al menos 10 caracteres.'),
});

type ContactFormData = z.infer<typeof contactFormSchema>;

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" disabled={pending} className="w-full bg-primary hover:bg-primary/90">
      {pending ? 'Enviando...' : 'Enviar Mensaje'}
    </Button>
  );
}

export default function ContactSection() {
  const { toast } = useToast();
  const [formState, formAction] = useFormState<ContactFormState, FormData>(submitContactForm, {
    message: '',
    success: false,
  });

  const { register, handleSubmit, formState: { errors }, reset } = useForm<ContactFormData>({
    resolver: zodResolver(contactFormSchema),
    defaultValues: {
      name: '',
      email: '',
      message: '',
    }
  });

  useEffect(() => {
    if (formState.message) {
      toast({
        title: formState.success ? 'Éxito' : 'Error',
        description: formState.message,
        variant: formState.success ? 'default' : 'destructive',
      });
      if (formState.success) {
        reset(); // Reset form fields on success
      }
    }
  }, [formState, toast, reset]);
  
  const onSubmit = (data: ContactFormData) => {
    const formData = new FormData();
    Object.entries(data).forEach(([key, value]) => {
      formData.append(key, String(value));
    });
    formAction(formData);
  };


  return (
    <section id="contact" className="py-16 md:py-24 bg-card">
      <div className="container mx-auto px-4 md:px-6">
        <h2 className="font-headline text-3xl md:text-4xl font-bold text-center mb-4 text-primary">
          Contáctanos
        </h2>
        <p className="text-center text-lg text-foreground/80 mb-12 max-w-2xl mx-auto">
          ¿Tienes preguntas? Estamos aquí para ayudarte. Ponte en contacto con nosotros a través de cualquiera de los siguientes medios.
        </p>

        <div className="grid md:grid-cols-2 gap-12 items-start">
          <div className="space-y-6">
            <h3 className="font-headline text-2xl font-semibold text-foreground">Información de Contacto</h3>
            <div className="flex items-start gap-4">
              <MapPin className="h-6 w-6 text-primary mt-1 shrink-0" />
              <div>
                <h4 className="font-semibold">Dirección</h4>
                <p className="text-foreground/80">Carrera 30A No 41B - 39, Barrio La Grama, Villavicencio, Meta, Colombia.</p>
              </div>
            </div>
            <div className="flex items-start gap-4">
              <Phone className="h-6 w-6 text-primary mt-1 shrink-0" />
              <div>
                <h4 className="font-semibold">Teléfono</h4>
                <p className="text-foreground/80">6086611623 EXT(401)</p>
              </div>
            </div>
            <div className="flex items-start gap-4">
              <Mail className="h-6 w-6 text-primary mt-1 shrink-0" />
              <div>
                <h4 className="font-semibold">Correo Electrónico</h4>
                <p className="text-foreground/80">
                  <a href="mailto:centrodeidiomas@unillanos.edu.co" className="hover:text-primary transition-colors">
                    centrodeidiomas@unillanos.edu.co
                  </a>
                </p>
              </div>
            </div>
            <h3 className="font-headline text-xl font-semibold text-foreground pt-4">Síguenos en Redes Sociales</h3>
            <div className="flex space-x-4">
              <Link href="https://www.facebook.com/unillanosoficial" target="_blank" rel="noopener noreferrer" aria-label="Facebook de UNILLANOS" className="text-foreground/80 hover:text-primary transition-colors">
                <Facebook className="h-8 w-8" />
              </Link>
              <Link href="https://www.instagram.com/unillanosoficial" target="_blank" rel="noopener noreferrer" aria-label="Instagram de UNILLANOS" className="text-foreground/80 hover:text-primary transition-colors">
                <Instagram className="h-8 w-8" />
              </Link>
            </div>
          </div>

          <Card className="shadow-xl">
            <CardHeader>
              <CardTitle className="font-headline text-2xl">Envíanos un Mensaje</CardTitle>
              <CardDescription>Completa el formulario para consultas generales.</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div>
                  <Label htmlFor="contact-name">Nombre</Label>
                  <Input id="contact-name" {...register('name')} placeholder="Tu nombre" className="mt-1" />
                  {errors.name && <p className="text-sm text-destructive mt-1">{errors.name.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('nombre')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('nombre'))}</p>}
                </div>

                <div>
                  <Label htmlFor="contact-email">Correo Electrónico</Label>
                  <Input id="contact-email" type="email" {...register('email')} placeholder="tu@correo.com" className="mt-1" />
                  {errors.email && <p className="text-sm text-destructive mt-1">{errors.email.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('correo')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('correo'))}</p>}
                </div>

                <div>
                  <Label htmlFor="contact-message">Mensaje</Label>
                  <Textarea
                    id="contact-message"
                    {...register('message')}
                    placeholder="Escribe tu consulta aquí..."
                    className="mt-1 min-h-[120px]"
                  />
                  {errors.message && <p className="text-sm text-destructive mt-1">{errors.message.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('mensaje')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('mensaje'))}</p>}
                </div>
                
                <SubmitButton />
                {formState.message && !formState.success && <p className="text-sm text-destructive mt-2 text-center">{formState.message}</p>}
              </form>
            </CardContent>
          </Card>
        </div>
      </div>
    </section>
  );
}
