'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { recommendCourses, type CourseRecommenderInput, type CourseRecommenderOutput } from '@/ai/flows/course-recommender';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Loader2, Sparkles } from 'lucide-react';

const recommenderSchema = z.object({
  goals: z.string().min(10, 'Por favor, describe tus metas con más detalle (mínimo 10 caracteres).'),
  proficiencyLevel: z.string().min(1, 'Debes seleccionar tu nivel de fluidez.'),
  availability: z.string().min(1, 'Debes seleccionar tu disponibilidad.'),
});

type RecommenderFormData = z.infer<typeof recommenderSchema>;

const proficiencyLevels = ['Principiante', 'Intermedio', 'Avanzado'];
const availabilityOptions = ['Mañanas', 'Tardes', 'Noches', 'Fines de semana', 'Tiempo completo'];

export default function AiCourseRecommenderSection() {
  const [recommendation, setRecommendation] = useState<CourseRecommenderOutput | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const { register, handleSubmit, formState: { errors }, setValue, watch } = useForm<RecommenderFormData>({
    resolver: zodResolver(recommenderSchema),
  });

  const onSubmit = async (data: RecommenderFormData) => {
    setIsLoading(true);
    setError(null);
    setRecommendation(null);
    try {
      const result = await recommendCourses(data);
      setRecommendation(result);
    } catch (e) {
      console.error("Error fetching recommendations:", e);
      setError('No se pudieron obtener las recomendaciones. Inténtalo de nuevo más tarde.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <section id="ai-recommender" className="py-16 md:py-24 bg-background">
      <div className="container mx-auto px-4 md:px-6">
        <div className="text-center mb-12">
          <Sparkles className="mx-auto h-12 w-12 text-primary mb-4" />
          <h2 className="font-headline text-3xl md:text-4xl font-bold text-primary mb-4">
            Descubre tu Curso Ideal con IA
          </h2>
          <p className="text-lg text-foreground/80 max-w-2xl mx-auto">
            ¿No estás seguro por dónde empezar? Nuestra IA te ayudará a encontrar el curso perfecto según tus metas, nivel y disponibilidad.
          </p>
        </div>

        <div className="grid md:grid-cols-2 gap-8 items-start">
          <Card className="shadow-xl">
            <CardHeader>
              <CardTitle className="font-headline text-2xl">Cuéntanos sobre ti</CardTitle>
              <CardDescription>Completa esta información para recibir una recomendación personalizada.</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div>
                  <Label htmlFor="goals">¿Cuáles son tus metas al aprender un nuevo idioma?</Label>
                  <Textarea
                    id="goals"
                    {...register('goals')}
                    placeholder="Ej: Viajar, mejorar oportunidades laborales, entender películas, etc."
                    className="mt-1 min-h-[100px]"
                  />
                  {errors.goals && <p className="text-sm text-destructive mt-1">{errors.goals.message}</p>}
                </div>

                <div>
                  <Label htmlFor="proficiencyLevel">Nivel de Fluidez Actual</Label>
                  <Select onValueChange={(value) => setValue('proficiencyLevel', value, { shouldValidate: true })} value={watch('proficiencyLevel')}>
                    <SelectTrigger id="proficiencyLevel" className="mt-1">
                      <SelectValue placeholder="Selecciona tu nivel" />
                    </SelectTrigger>
                    <SelectContent>
                      {proficiencyLevels.map(level => (
                        <SelectItem key={level} value={level}>{level}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.proficiencyLevel && <p className="text-sm text-destructive mt-1">{errors.proficiencyLevel.message}</p>}
                </div>

                <div>
                  <Label htmlFor="availability">¿Cuándo tienes tiempo para estudiar?</Label>
                   <Select onValueChange={(value) => setValue('availability', value, { shouldValidate: true })} value={watch('availability')}>
                    <SelectTrigger id="availability" className="mt-1">
                      <SelectValue placeholder="Selecciona tu disponibilidad" />
                    </SelectTrigger>
                    <SelectContent>
                      {availabilityOptions.map(option => (
                        <SelectItem key={option} value={option}>{option}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.availability && <p className="text-sm text-destructive mt-1">{errors.availability.message}</p>}
                </div>

                <Button type="submit" disabled={isLoading} className="w-full bg-primary hover:bg-primary/90">
                  {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Sparkles className="mr-2 h-4 w-4" /> }
                  {isLoading ? 'Buscando...' : 'Obtener Recomendación'}
                </Button>
              </form>
            </CardContent>
          </Card>

          <div className="mt-8 md:mt-0">
            {isLoading && (
              <div className="flex justify-center items-center h-64">
                <Loader2 className="h-12 w-12 text-primary animate-spin" />
              </div>
            )}
            {error && (
              <Card className="border-destructive bg-destructive/10">
                <CardHeader>
                  <CardTitle className="text-destructive">Error</CardTitle>
                </CardHeader>
                <CardContent>
                  <p className="text-destructive">{error}</p>
                </CardContent>
              </Card>
            )}
            {recommendation && (
              <Card className="bg-primary/5 shadow-lg">
                <CardHeader>
                  <CardTitle className="font-headline text-2xl text-primary">Cursos Recomendados para Ti</CardTitle>
                </CardHeader>
                <CardContent className="prose prose-sm max-w-none text-foreground/90">
                  <p><strong>Cursos Sugeridos:</strong> {recommendation.recommendedCourses}</p>
                  <p><strong>Justificación:</strong> {recommendation.reasoning}</p>
                  <Button asChild className="mt-4 bg-accent hover:bg-accent/90 text-accent-foreground">
                    <a href="#enrollment">Inscribirme Ahora</a>
                  </Button>
                </CardContent>
              </Card>
            )}
             {!isLoading && !error && !recommendation && (
                <Card className="border-dashed border-gray-300">
                    <CardContent className="py-12 text-center text-muted-foreground">
                        <Sparkles className="mx-auto h-10 w-10 mb-3" />
                        <p>Tus recomendaciones personalizadas aparecerán aquí.</p>
                    </CardContent>
                </Card>
            )}
          </div>
        </div>
      </div>
    </section>
  );
}
