'use client';

import { useEffect } from 'react';
import { useFormState, useFormStatus } from 'react-dom';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { submitEnrollmentForm, type EnrollmentFormState } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea'; // Textarea isn't used but kept for consistency if needed
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from '@/hooks/use-toast';
import Link from 'next/link';

const enrollmentFormSchema = z.object({
  fullName: z.string().min(3, 'Nombre completo es requerido.'),
  age: z.coerce.number().min(5, 'Edad debe ser al menos 5 años.').max(100, 'Edad no puede ser mayor a 100.'),
  proficiencyLevel: z.string().min(1, 'Nivel de conocimiento es requerido.'),
  email: z.string().email('Correo electrónico inválido.'),
  phone: z.string().min(7, 'Teléfono debe tener al menos 7 dígitos.'),
  language: z.string().min(1, 'Debe seleccionar un idioma.'),
  // 'course' field is derived from language or can be more specific if needed
});

type EnrollmentFormData = z.infer<typeof enrollmentFormSchema>;

const proficiencyLevels = ['Principiante (A1)', 'Básico (A2)', 'Intermedio (B1)', 'Intermedio-Alto (B2)', 'Avanzado (C1)', 'Muy Avanzado (C2)'];
const languages = [
  { id: 'ingles', name: 'Inglés' },
  { id: 'frances', name: 'Francés' },
  { id: 'aleman', name: 'Alemán' },
  { id: 'portugues', name: 'Portugués' },
];


function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" disabled={pending} className="w-full bg-primary hover:bg-primary/90">
      {pending ? 'Enviando...' : 'Inscribirme Ahora'}
    </Button>
  );
}

export default function EnrollmentSection() {
  const { toast } = useToast();
  const [formState, formAction] = useFormState<EnrollmentFormState, FormData>(submitEnrollmentForm, {
    message: '',
    success: false,
  });

  const { register, handleSubmit, formState: { errors }, setValue, reset, watch } = useForm<EnrollmentFormData>({
    resolver: zodResolver(enrollmentFormSchema),
    defaultValues: {
      fullName: '',
      age: undefined,
      proficiencyLevel: '',
      email: '',
      phone: '',
      language: '',
    }
  });
  
  const selectedLanguage = watch('language');

  useEffect(() => {
    if (formState.message) {
      toast({
        title: formState.success ? 'Éxito' : 'Error',
        description: formState.message,
        variant: formState.success ? 'default' : 'destructive',
      });
      if (formState.success) {
        reset(); // Reset form fields on success
      }
    }
  }, [formState, toast, reset]);
  
  // For react-hook-form to work with server actions, we need to pass the course as part of the FormData manually
  // or adjust the server action to not expect 'course' if language is enough.
  // For simplicity, we'll add a hidden input for 'course' which will be same as 'language'.
  // In a real scenario, 'course' might be more specific (e.g., "Inglés B1 Intensivo").
  
  const onSubmit = (data: EnrollmentFormData) => {
    const formData = new FormData();
    Object.entries(data).forEach(([key, value]) => {
      formData.append(key, String(value));
    });
    // Add 'course' to formData, using selected language as placeholder
    formData.append('course', data.language || 'No especificado');
    formAction(formData);
  };


  return (
    <section id="enrollment" className="py-16 md:py-24 bg-background">
      <div className="container mx-auto px-4 md:px-6">
        <h2 className="font-headline text-3xl md:text-4xl font-bold text-center mb-4 text-primary">
          Proceso de Inscripción
        </h2>
        <p className="text-center text-lg text-foreground/80 mb-12 max-w-3xl mx-auto">
          Sigue estos sencillos pasos para unirte a nuestros cursos y comenzar tu viaje de aprendizaje de idiomas.
          Las fechas de inscripción son del 1 al 15 de cada mes. Horarios de atención: Lunes a Viernes de 8 AM a 5 PM.
        </p>

        <div className="grid md:grid-cols-2 gap-12 items-start">
          <div className="prose prose-lg text-foreground/90 max-w-none">
            <h3 className="font-semibold text-xl text-foreground">Pasos para Inscribirte:</h3>
            <ol className="list-decimal list-inside space-y-3">
              <li>Completa el formulario de inscripción en línea con tus datos personales y selecciona el curso de tu interés.</li>
              <li>Revisa tu correo electrónico para la confirmación de pre-inscripción y los detalles de pago.</li>
              <li>Realiza el pago correspondiente a través de los medios indicados.</li>
              <li>Envía el comprobante de pago a nuestro correo electrónico: <a href="mailto:pagos.idiomas@unillanos.edu.co" className="text-primary hover:underline">pagos.idiomas@unillanos.edu.co</a></li>
              <li>Recibirás la confirmación final de tu inscripción y los detalles de inicio de clases.</li>
            </ol>
            <p className="mt-6 text-sm">
              Al enviar este formulario, aceptas nuestra{' '}
              <Link href="/privacy-policy" className="text-primary hover:underline">Política de Privacidad</Link> y nuestros{' '}
              <Link href="/terms-conditions" className="text-primary hover:underline">Términos y Condiciones</Link>.
            </p>
          </div>

          <Card className="shadow-xl">
            <CardHeader>
              <CardTitle className="font-headline text-2xl">Formulario de Inscripción</CardTitle>
              <CardDescription>Completa tus datos para iniciar tu proceso.</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div>
                  <Label htmlFor="fullName">Nombre Completo</Label>
                  <Input id="fullName" {...register('fullName')} placeholder="Tu nombre completo" className="mt-1" />
                  {errors.fullName && <p className="text-sm text-destructive mt-1">{errors.fullName.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('nombre')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('nombre'))}</p>}
                </div>

                <div>
                  <Label htmlFor="age">Edad</Label>
                  <Input id="age" type="number" {...register('age')} placeholder="Tu edad" className="mt-1" />
                  {errors.age && <p className="text-sm text-destructive mt-1">{errors.age.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('edad')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('edad'))}</p>}
                </div>
                
                <div>
                  <Label htmlFor="proficiencyLevel">Nivel de Conocimiento del Idioma</Label>
                  <Select onValueChange={(value) => setValue('proficiencyLevel', value, { shouldValidate: true })} value={watch('proficiencyLevel')}>
                    <SelectTrigger id="proficiencyLevel" className="mt-1">
                      <SelectValue placeholder="Selecciona tu nivel" />
                    </SelectTrigger>
                    <SelectContent>
                      {proficiencyLevels.map(level => (
                        <SelectItem key={level} value={level}>{level}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.proficiencyLevel && <p className="text-sm text-destructive mt-1">{errors.proficiencyLevel.message}</p>}
                   {formState.issues?.find(issue => issue.toLowerCase().includes('nivel')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('nivel'))}</p>}
                </div>

                <div>
                  <Label htmlFor="email">Correo Electrónico</Label>
                  <Input id="email" type="email" {...register('email')} placeholder="tu@correo.com" className="mt-1" />
                  {errors.email && <p className="text-sm text-destructive mt-1">{errors.email.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('correo')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('correo'))}</p>}
                </div>

                <div>
                  <Label htmlFor="phone">Teléfono</Label>
                  <Input id="phone" type="tel" {...register('phone')} placeholder="Tu número de teléfono" className="mt-1" />
                  {errors.phone && <p className="text-sm text-destructive mt-1">{errors.phone.message}</p>}
                  {formState.issues?.find(issue => issue.toLowerCase().includes('teléfono')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('teléfono'))}</p>}
                </div>
                
                <div>
                  <Label htmlFor="language">Idioma de Interés</Label>
                  <Select onValueChange={(value) => setValue('language', value, { shouldValidate: true })} value={selectedLanguage}>
                    <SelectTrigger id="language" className="mt-1">
                      <SelectValue placeholder="Selecciona un idioma" />
                    </SelectTrigger>
                    <SelectContent>
                      {languages.map(lang => (
                        <SelectItem key={lang.id} value={lang.id}>{lang.name}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.language && <p className="text-sm text-destructive mt-1">{errors.language.message}</p>}
                   {formState.issues?.find(issue => issue.toLowerCase().includes('idioma') || issue.toLowerCase().includes('curso')) && <p className="text-sm text-destructive mt-1">{formState.issues.find(issue => issue.toLowerCase().includes('idioma') || issue.toLowerCase().includes('curso'))}</p>}
                </div>
                
                {/* Hidden input for course, can be made more sophisticated */}
                <input type="hidden" {...register('course' as any)} value={selectedLanguage || ''} />


                <SubmitButton />
                {formState.message && !formState.success && <p className="text-sm text-destructive mt-2 text-center">{formState.message}</p>}
              </form>
            </CardContent>
          </Card>
        </div>
      </div>
    </section>
  );
}
